import React, {useEffect} from 'react';
import '../index.css';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {ImArrowUp} from "react-icons/im";

const HomeWrap = styled.div`
    padding: 110px 0 70px; 
    .gotop {
        position: fixed; right: 30px; bottom: 50px; z-index: 99999;
        a {background: #ffc300; color: #fff; text-align: center;
            font-size: 20px; padding: 5px 8px; transition: all 0.5s;
            &:hover {background: #009223}
        }
`;

   
const Article1 = styled.div`
    margin-bottom: 30px;
    .slide {
        height: 440px; position: relative;
        background-size : cover;
        background-repeat : no-reapt;
        background-position: center;
        &.slide1 {background-image:url('images/slide_1.png')};
        &.slide2 {background-image:url('images/slide_2.jpg')};
        &.slide3 {background-image:url('images/slide_3.jpg')};
        &.slide4 {background-image:url('images/slide_4.png')};
        }
    .slick-dots {
        position: absolute; 
        bottom: 20px; left: 50%; 
        transform: translate(-50%);
        li {
            display: inline-block; padding: 0 5px;
            button {
                width: 10px; height: 10px; border-radius: 30px; 
                background: #fff; 
                text-indent: -99999px;
                overflow: hidden;
            }
            &.slick-active {
                button {background: #ffc300; width: 30px; border-radius: 30px; transition: all 0.5s;}
                }
            }
        }
`;

const Article2 = styled.div`
    margin-bottom: 80px;
    h2 {
        color: #292929;
        font-size: 40px;
        font-weight: bold;
        background: url(images/bul_tit.png) 20px 35px no-repeat;
        padding: 40px 0 0 45px;
        margin-top: 20px;
    }
    .menulist {justify-content: space-around; display: flex; flex-wrap: wrap;
                margin: 20px; font-family: 'Farro', sans-serif;
        li {
            height: 115px; margin: 20px 0; width: 330px;
            text-align: center; font-size: 25px; padding: 35px 0 30px; border: 10px solid #ffc300; 
            border-radius: 20px; transition: all 0.5s;
            &:hover {background: transparent; padding: 35px 0 30px; background: #ffc300; color: #fff }
        }
    }
`;

const Article3 = styled.div`
    background: url(images/bg_content.gif) 0 0 repeat; 
    background-color: #666; padding: 80px 0; margin-bottom: 80px;
    .a3_content {
        .top_content {display: flex; justify-content: center;
            .top_howto {
                background: url(images/bg_howto.png) 0 0 no-repeat;
                width: 400px;
                height: 300px;
                position: relative;
                margin-left: 55px;
                p {
                    color: #fff;
                    font-size: 28px;
                    font-weight: bold;
                    line-height: 40px;
                    letter-spacing: -0.03em;
                    position: absolute;
                    left: 39px;
                    top: 38px;}
                .btn {
                    position: absolute;
                    left: 41px;
                    bottom: 42px;
                    border: 2px solid #fff;
                    border-radius: 20px;
                    width: 99px; transition: all 0.5s;
                    span {
                        display: block;
                        height: 36px;
                        text-align: center;
                        line-height: 36px;
                        color: #fff;
                        font-size: 16px;
                        font-weight: 300;
                    }
                &:hover {background: #ffc300}
                }
            }
            .top_histroy {
                width: 825px;
                position: relative;
                background: url(images/img_history.png) 0 0 no-repeat;
                height: 300px;
                position: relative;
                p {
                    color: #fff;
                    font-size: 28px;
                    font-weight: bold;
                    line-height: 40px;
                    letter-spacing: -0.03em;
                    position: absolute;
                    left: 410px;
                    top: 38px;
                }
                .btn {
                    position: absolute;
                    left: 410px;
                    bottom: 42px;
                    border: 2px solid #fff;
                    border-radius: 20px;
                    width: 128px; transition: all 0.5s;
                    span {
                        display: block;
                        height: 36px;
                        line-height: 36px;
                        text-align: center;
                        color: #fff;
                        font-size: 16px;
                        font-weight: 300;
                    }
                    &:hover {background: #ffc300}
                }
            }
        }
        .bottom_content {margin-top: 30px; display: flex; justify-content: center;
            .bottom_news { 
                background-color: #ddd;
                width: 300px;
                height: 300px;
                border-radius: 30px 0 30px 0;
                position: relative;
                .btm_title {
                    position: absolute;
                    top: 75px;
                    left: 60px;
                    h3 {
                        color: #292929;
                        font-size: 30px;
                        font-weight: bold;
                        font-family: font_sw;
                        background: url(images/bul_tit.png) 0 0 no-repeat;
                        min-height: 60px;
                        padding: 17px 0 20px 25px;
                    }
                    p {
                        text-align: center; line-height: 26px; letter-spacing: -0.04em;
                    }
                    .btn {
                        position: absolute;
                        left: 30px;
                        bottom: -52px;
                        border: 2px solid #fff;
                        border-radius: 20px;
                        width: 128px;
                        transition: all 0.5s;
                        span {
                            display: block;
                            height: 36px;
                            line-height: 36px;
                            text-align: center;
                            color: #fff;
                            font-size: 16px;
                            font-weight: 300;
                        }
                    &:hover {background: #ffc300}
                    }
                }
            }
            .bottom_ad {
                width: 540px;
                height: 300px;
                margin-right: 30px;
                margin-left: 30px;
                position: relative;
                border-radius: 0 30px 0 30px;
                background: url(images/coolj.png) 96px 0 no-repeat #7ca723;
                p {
                    color: #fff;
                    font-size: 28px;
                    font-weight: bold;
                    line-height: 40px;
                    letter-spacing: -0.03em;
                    position: absolute;
                    left: 50px;
                    top: 38px;
                }
                .btn {
                    position: absolute;
                    left: 350px;
                    bottom: 42px;
                    border: 2px solid #fff;
                    border-radius: 20px;
                    width: 128px;
                    transition: all 0.5s;
                    span {
                        display: block;
                        height: 36px;
                        line-height: 36px;
                        text-align: center;
                        color: #fff;
                        font-size: 16px;
                        font-weight: 300;
                    }
                &:hover {background: #ffc300}
                }
            }
            .bottom_logo {
                width: 270px;
                height: 300px;
                position: relative;
                overflow: hidden;
                border-radius: 0 30px 0 30px;
            }
        }
    }
`;


const Homesection = () => {

    useEffect(() => {
        window.scrollTo(0,0)
    },[])

    const settings = {
        dots:true,
        infinite:true,
        speed:600,
        autoplay:true,
        autoplaySpeed:4000,
        slidesToShow:1,
        slidesToScroll:1,
        arrows: false
    }

    return (
        <HomeWrap>
            <Article1 classname="article1">
                <Slider {...settings}>
                    <div className="slide slide1"></div>
                    <div className="slide slide2"></div>
                    <div className="slide slide3"></div>
                    <div className="slide slide4"></div>
                </Slider>
            </Article1>
            <Article2 classname="article2">
                <div className="row">
                    <h2>Subway's Menu</h2>
                    <ul className="menulist">
                        <li><Link to="/menu">Sandwich</Link></li>
                        <li>Wrap</li>
                        <li>Chopped Salad</li>
                        <li>Morning Menu</li>
                        <li>Smile Sub</li>
                        <li>Catering Menu</li>
                    </ul>
                </div>
            </Article2>
            <Article3 classname="article3">
                <div className="a3_content cf">
                    <div className="top_content cf">
                        <div className="top_howto">
                            <p>써브웨이를 <br />
                                제대로 즐기는 방법!
                            </p>
                            <Link to="/howto" className="btn"><span>이용방법</span></Link>
                        </div>
                        <div className="top_histroy">
                            <p>50년 역사를 가진 <br />
                                No.1 프랜차이즈의 성장기
                            </p>
                            <Link to="#none" className="btn"><span>써브웨이 역사</span></Link>
                        </div>
                    </div>
                    <div className="bottom_content">
                        <div className="bottom_news">
                            <div className="btm_title">
                                <h3>What's New</h3>
                                <p>써브웨이의 다양한 소식을<br />
                                    빠르게 전달해드립니다.</p>
                                <Link to="/notice" className="btn"><span>공지사항</span></Link>
                            </div>
                        </div>
                        <div className="bottom_ad">
                            <p>지금 바로, <br />
                                쿨제이 보러 가실래요?
                            </p>
                            <Link to="/advert" className="btn"><span>광고영상</span></Link>
                        </div>
                        <div className="bottom_logo">
                            <img src="images/big_logo.png" alt="" />
                        </div>
                    </div>
                </div>
                </Article3>
                <div className="gotop">
                    <a href="#"><ImArrowUp /></a>
                </div>
        </HomeWrap>
    );
};

export default Homesection;