import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import '../index.css';
import {FaInstagram, FaFacebookF} from "react-icons/fa";


const FooterBlock = styled.div`
    background: #333; color: #888; font-size: 14px; position: relative;
    .ft1 {
        padding: 20px 0;
        a {padding-right: 20px; transition: all 0.5s;
            &:hover {color: #fff}
        }
    }
    .ft2 {padding: 20px 0; display: flex;
        .info { width: 80%;
            span {
                position: relative; padding-right: 20px; font-size: 13px;
                &::after {
                    content: ''; position: absolute; right: 9px; top: 5px;
                    width: 2px; height: 10px; background-color: #666;
                    }
                &:nth-child(4)::after {display: none;}
                }
            p {padding: 10px 0; font-size: 13px;}
                &::after {
                    content: ''; position: absolute; left: 0; top: 59px; 
                    width: 100%; height: 1px; background: #3b3b3b;
                    }
                }
            }
        .sns {width: 20%; align-self: center;
            a {color: #fff;
                .insta {background: #ad3a72; margin: 0 10px; border-radius: 50%; 
                        font-size: 50px; padding: 10px 0; transition: all 0.5s;
                    &:hover {background: #dd257b}
                }
                .facebook {background: #324160; border-radius: 50%; 
                    font-size: 50px; padding: 10px 0; transition: all 0.5s;
                    &:hover {background: #3b5997}
                }
            }
        }
`;

const Footer = () => {
    return (
        <FooterBlock>
            <div className="row">
                <div className="ft1">
                    <Link to="">이용약관</Link>
                    <Link to="">개인정보처리방침</Link>
                    <Link to="">써브카드</Link>
                    <Link to="">점주관리자</Link>
                    <Link to="">Subway Listens</Link>
                </div>
                <div className="ft2">
                    <div className="info">
                        <span>서울시 서초구 강남대로 535 프린스빌딩 15층</span>
                        <span>대표 : 콜린클락</span>
                        <span>전화 : 02-797-5036</span>
                        <span>사업자등록번호 : 101-84-04143</span>
                        <p>SUBWAY® is a Registered Trademark of Subway IP LLC. © 2021 Subway IP LLC. All Rights Reserved.</p>
                    </div>
                    <div className="sns">
                        <Link to="https://www.instagram.com/subwaykorea"><FaInstagram className="insta" /></Link>
                        <Link to="https://www.facebook.com/Subwaykr"><FaFacebookF className="facebook" /></Link>
                    </div>
                </div>
            </div>
        </FooterBlock>
    );
};

export default Footer;