import React from 'react';
import {Link} from 'react-router-dom';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import styled from 'styled-components';
import '../index.css';
import {BiLeftArrow, BiRightArrow, BiSearchAlt2} from 'react-icons/bi'

const HowtoBlock = styled.div`
    padding: 150px 0;  background: #f2f2f2;
    position: relative;
    h2 {font-size: 38px; text-align: center; margin-bottom: 50px}
        .slick-active {
            .slide {opacity: 1;}
        }
        .slick-current {
            .slide {transform: scale(1);}
        }
    .slide {
        margin: 150px 0 110px;
        transform: scale(0.8); opacity: 0.6; transition: all 0.5s; 
        img {margin-left: 60px}
        .howto {width: 370px; height: 500px; background: #fff; position: fixed; z-index: 99999; 
                border: 3px double #555; margin-left: 150px; padding: 20px;
                left: 50%; transform: translateY(-80%);
            &::before {content: ''; border: 20px solid #666; border-top-color: transparent;
                        border-bottom-color: transparent; border-left-color: transparent;
                        position: absolute; bottom: 60%; left: -6%; transform: translate(-50%);}
            li {padding: 10px;
                &:nth-child(1) {font-size: 22px; font-weight: 700; color: #009223; }
                &:nth-child(2) {position: relative; font-size: 28px; font-weight: bold; padding-bottom: 20px;
                    p {font-size: 20px}
                }
                &:nth-child(2)::after {content: ''; width: 40px; height: 4px; background: #ffc300;
                                         position: absolute; bottom: -10%; left: 4%; 
                                     p {font-size: 15px; display: inline-block;}
                                    } 
                &:nth-child(3) {line-height: 1.5; padding-top: 28px;}
                &:nth-child(4) {position: absolute; bottom: 30%;} 
                }
                button {border: 2px solid #ffc300; background: #fff; padding: 5px 9px; 
                        border-radius: 20px; font-size: 15px; margin-right: 8px;
                    .ser {color: #333; margin-left: 5px; vertical-align: text-bottom} 
                    &:hover {background: #ffc300; color: #fff;}
                }
            }
        }
        .slick-arrow { 
                position: absolute; top: 50%; transform: translateY(-50%);
                background: none; padding: 10px; 
            z-index: 99999; width: 50px;  height: 50px; color: #555;
        &.slick-prev {left: 50px; transition: all 0.5s;
            &:hover {color: #ffc300;}
        }
        &.slick-next {right: 50px; transition: all 0.5s;
            &:hover {color: #ffc300;}
        }    
    }
    .slick-dots {
        position: absolute; top: 0px; left: 50%; transform: translate(-50%);
        li{
            display: inline-block;
            width: 70px; height: 70px; background: #fff; text-align: center; line-height: 70px;
            border: 3px solid #aaa; border-radius: 50%; padding: 0px 5px 0px;
            position: relative; margin-right: 70px; color: #aaa;
        &:nth-child(5) {margin-right: 0;}
        &:nth-child(5)::after {display: none;}
        &.slick-active {background: #009223; border: 3px solid #009223; color: #fff;
            &::after {content: ''; position: absolute; right: -85%; 
                       top: 33px; background: #009223; width: 30px; height: 5px;}
            button {font-size: 33px; font-weight: 600; background: none; color: #fff;}            
            }  
            button {font-size: 33px; font-weight: 600; background: none; color: #aaa}
        }
    }
`;

const Howtosection = () => {
    const settings = {
        autoplay: false, 
        autoplaySpeed: 3000,
        dots: true,
        speed: 600, 
        slidesToShow: 1, 
        slidesToScroll: 1, 
        cssEase: 'linear', 
        arrows: true, 
        prevArrow: <BiLeftArrow />,
        nextArrow: <BiRightArrow />,
        centerMode: true,
        centerPadding: "200px"
    }


    return (
        <HowtoBlock>
            <h2>이용방법</h2>
            <Slider {...settings}>
                <div className="slide slide1">
                    <img src="images/img_store_order01.png" alt="" />
                    <ul className="howto">
                        <li>Step 1</li>
                        <li>메뉴 선택</li>
                        <li>메뉴를 고르세요.<br />
                            샌드위치(15cm 또는 30cm)와 찹샐러드 중 선택 가능합니다.</li>
                        <li>
                            <button><Link to="#none">샌드위치 메뉴<BiSearchAlt2 className="ser" /></Link></button>
                            <button><Link to="#none">찹샐러드 메뉴<BiSearchAlt2 className="ser" /></Link></button>
                        </li>
                    </ul>
                </div>
                <div className="slide slide2">
                    <img src="images/img_store_order02.png" alt="" />
                    <ul className="howto">
                        <li>Step 2</li>
                        <li>빵 선택 <p>(찹샐러드 선택 시 제외)</p></li>
                        <li>매장에서 직접 구운 6가지 종류 중 <br />
                            고객님이 원하는 빵으로 <br />
                            추가비용 없이 선택 가능합니다.</li>
                        <li>
                            <button><Link to="#none">빵종류<BiSearchAlt2 className="ser" /></Link></button>
                        </li>
                    </ul>
                </div>
                <div className="slide slide3">
                    <img src="images/img_store_order03.png" alt="" />
                    <ul className="howto">
                        <li>Step 3</li>
                        <li>추가토핑 선택</li>
                        <li>나만의 메뉴를 더욱 풍성하게 즐기세요.</li>
                        <li>
                        <button><Link to="#none">추가 토핑 메뉴<BiSearchAlt2 className="ser" /></Link></button>
                        </li>
                    </ul>
                </div>
                <div className="slide slide4">
                    <img src="images/img_store_order04.png" alt="" />
                    <ul className="howto">
                        <li>Step 4</li>
                        <li>야채&amp;소스 선택</li>
                        <li>나만의 스타일을 완성하는 단계! <br />
                            원하지 않는 야채는 빼고 좋아하는 야채는 더하세요.<br />
                            오늘의 기분에 맞는 소스를 선택해주세요.</li>
                        <li>
                            <button><Link to="#none">야채종류<BiSearchAlt2 className="ser" /></Link></button>
                            <button><Link to="#none">소스종류<BiSearchAlt2 className="ser" /></Link></button>
                        </li>
                    </ul>
                </div>
                <div className="slide slide5">
                    <img src="images/img_store_order05.png" alt="" />
                    <ul className="howto">
                        <li>Step 5</li>
                        <li>세트 선택</li>
                        <li>세트로 구매하시면 할인혜택을 받으실 수 있습니다.<br />
                            세트로 더욱 든든하고 알차게 즐기세요.</li>
                        <li>
                            <button><Link to="#none">스마일썹<BiSearchAlt2 className="ser" /></Link></button>
                        </li>
                    </ul>
                </div>

            </Slider>
        </HowtoBlock>
    );
};

export default Howtosection;