import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const MenuItemBlock = styled.div`
    ul { 
        li { 
            width: 370px;
            height: 330px;
            background-color: #fff;
            float: left;
            overflow: hidden;
            text-align: center;
            position: relative;
            margin: 18px 20px;
            flex-wrap: wrap;
            .img {    
                width: 270px;
                height: 189px;
                margin: 20px auto 0;
                position: relative;
                z-index: 1;
                img {width: 100%; height: 100%}
            }
            .name {    
                color: #292929;
                font-size: 22px;
                font-weight: bold;
                margin: 10px 0 7px;
                display: block;
                position: relative;
                z-index: 5;
                top: 0;
            }
            .nameen {
                color: #666666;
                font-size: 16px;
                font-family: font_sw;
                display: block;
                margin: 0 0 13px;
                letter-spacing: -0.06em;
                position: relative;
                z-index: 5;
                top: 0;
            }
            .cal {
                color: #ffc300;
                font-size: 18px;
                font-family: font_sw;
                font-weight: bold;
                display: block;
                letter-spacing: -0.05em;
                z-index: 1;
            }
            .summary {
                position: absolute;
                left: 20px;
                right: 20px;
                top: 171px;
                opacity: 0;
                z-index: 5;
                p {
                    color: #c5e698;
                    font-size: 16px;
                    letter-spacing: -0.05em;
                    line-height: 24px;
                    word-break: keep-all;
                }
            }
            .btn {
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                background: url(images/icon_view.png) 147px bottom no-repeat;
                width: 100%;
                height: 100%;
                opacity: 0;
                z-index: 5;
                transform: scale(0.5);
            }
            &:hover .name {color: #fff; top: -159px; transition-duration: 0.4s}
            &:hover .nameen {color: #fff; top: -160px; transition-duration: 0.4s}
            &:hover .summary {opacity: 1; top: 140px; transition-duration: 0.4s; transition-delay: 0.2s}
            &:hover .btn {opacity: 1; transform:scale(1); bottom: 6px; transition-duration: 0.3s; transition-delay: 0.3s}
            &:hover::after {opacity: 1; transition-duration: 0.2s;}
            &::after {
                content: '';
                background-color: #009223;
                position: absolute;
                left: 0;
                width: 100%;
                top: 0;
                height: 100%;
                z-index: 9;
                opacity: 0;
                z-index: 3;
            }
        }
    }

`;

const MenuItem = ({name, nameen, cal, photo, summary}) => {
    return (
        <MenuItemBlock>
            <ul>
                <li>
                    <div className="img">
                        <img src={photo} alt="" />
                    </div>
                    <strong className="name">{name}</strong>
                    <span className="nameen">{nameen}</span>
                    <span className="cal">{cal}</span>
                    <div className="summary">
                        <p>{summary}</p>
                    </div>
                    <Link to="#none" className="btn"></Link>
                </li>
            </ul>
        </MenuItemBlock>
    );
};

export default MenuItem;