import React from 'react';
import styled from 'styled-components';
import Button from './Button';
import {Link} from 'react-router-dom';

const MemberFormBlock = styled.div`
    max-width: 550px; margin-left: auto; margin-right: auto;
    margin-top: 70px;
    background: #eee; padding: 60px 20px 90px; border-radius: 10px;
    h3 {font-size: 38px; text-align: center; margin: 30px 0 60px;}
    p {margin-left: 60px}
`;

const StyledInput = styled.input`
    width: 80%; padding: 15px 10px; border-radius: 10px; 
    border: 1px solid #555; margin-bottom: 20px; transform: translateX(12.5%)
`;

const MemberFoot = styled.div`
    text-align: right; margin-top: 30px; margin-right: 20px;
    a {color: #333; text-decoration: underline;
        transition: all 0.5s;
        &:hover {color: #ffc10c;}
    }
`;

const textType = {
    login: '로그인',
    join: '회원가입'
}

const MemberForm = ({type}) => {
    const text = textType[type]
    return (
        <MemberFormBlock>
            <h3>{text}</h3>
            <form>
                {
                    type==='join' && <StyledInput type="text" placeholder="이름" name="username" />
                }
                <StyledInput type="text" placeholder="아이디" name="userid" />
                <StyledInput type="password" placeholder="비밀번호" name="password" />
                {
                    type==='join' && (
                        <>
                        <StyledInput type="password" placeholder="비밀번호확인" name="passok" />
                        <p><label>성별 : </label> 남 <input type="radio" name="gender" value="M" /> 여 <input type="radio" name="gender" value="F" /></p>
                        </>
                    )
                }
                <Button type="submit">{text}</Button>
            </form>
            <MemberFoot>
                {
                    type==='login' ? <Link to="/join">회원가입</Link> : <Link to="/login">로그인</Link>
                }
            </MemberFoot>
        </MemberFormBlock>
    );
};

export default MemberForm;