import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {RiLoginBoxLine} from "react-icons/ri";
import {IoMdPersonAdd} from "react-icons/io";

const HeaderBlock = styled.div`
    background: #fff;
    box-shadow: 0px 1px 2px #999;
    position: fixed; top: 0; left: 0; 
    padding: 30px 0;
    width: 100%; 
    z-index: 99999999;
    .row {
        display: flex;
        justify-content: space-between;
        align-items:center;
        .nav {
            .depth1 {display: flex};
                li {margin: 0 20px; position: relative; padding: 10px 0; 
                    font-size: 18px; font-weight: 700; transition: all 0.5s;
                    .depth2 {
                        position: absolute; 
                        top: 100%; left: 50%; transform: translateX(-50%); 
                        background: #fff;
                        width: 170px;
                        background: #fff;
                        padding: 15px 20px;
                        display: none;
                        li {color: #666; padding: 8px 0; text-align: center; 
                            font-size: 16px; font-weight: 600; transition: all 0.5s;
                        &:hover {color: #009223}
                        }
                    }
                &:hover {color: #009223}
                &:hover .depth2 {display: block}
                }
            }
        .mobileNav {
            padding:10px 20px; 
            border-radius:50px;
            font-size: 20px;
            display: flex;
            align-items: center;
            .enter {margin-right: 10px}
        }
        a:hover {color: #009223}
    }
`;



    

const Header = ({onSelect}) => {
   
    return (
        <HeaderBlock>
            <div className="row">
                <h1 className="logo">
                    <Link to="/">
                        <img src="images/logo.png" alt="써브웨이"/>
                    </Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1">
                        <li><Link to="/menu">메뉴소개</Link>
                            <ul className="depth2">
                                <li><Link to="/menu" onClick={()=>onSelect(0)}>샌드위치</Link></li>
                                <li><Link to="/menu" onClick={()=>onSelect(1)}>랩·기타</Link></li>
                                <li><Link to="/menu" onClick={()=>onSelect(2)}>찹샐러드</Link></li>
                                <li><Link to="/menu" onClick={()=>onSelect(3)}>아침메뉴</Link></li>
                                <li><Link to="/menu" onClick={()=>onSelect(4)}>스마일썹</Link></li>
                                <li><Link to="/menu" onClick={()=>onSelect(5)}>단체메뉴</Link></li>
                            </ul>
                        </li>
                        <li><Link to="/howto">이용방법</Link> </li>
                        <li><Link to="/notice">공지사항</Link> </li>
                        <li><Link to="/store">매장찾기</Link></li>
                        <li><Link to="/advert">광고영상</Link></li>
                    </ul>
                </nav>
                <div className="mobileNav">
                <Link to="/login"><RiLoginBoxLine className="enter" /></Link>
                <Link to="/join"><IoMdPersonAdd className="person" /></Link>
                </div>
            </div>
        </HeaderBlock>
    );
};

export default Header;