import React, { useEffect, useState, useCallback } from 'react';
import { getPostByNo } from '../components/Data';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';
import {GiHamburgerMenu} from "react-icons/gi";

const NoticeBlock = styled.div`
    padding-top: 180px
`;
const InfoBlock = styled.div`
    padding: 20px 0 150px;
    margin: 0 20px;
    .post-view-wrapper {
      width: 70%; 
      margin: 0 auto;
    h2 {font-size: 32px; margin-bottom: 5px; font-weight: 700}
    h3 {font-size: 20px; margin-bottom: 50px; color: #999}
    img {width: 90%; margin-bottom: 20px}
    p {margin-bottom: 30px}
    div {text-align: center}
    .post-view-go-list-btn {
      border: 0; font-size: 17px; margin: 20px auto;
      padding: 11px 25px; border-radius: 30px;
      &:hover {background: #0a7523}
      background: #009222; color: #fff;
      .goback {margin-right: 7px; vertical-align: middle;
              color: #ffc200; font-size: 30px}
    }
  }
`;

const PostView = ({ history, location, match }) => {
  const [ data, setData ] = useState({});

  const { no } = match.params;
  
  const [mid, setMid] = useState(0);
  
  const onSelect =  useCallback(
      (id) => {setMid(id)
      },[]
  );


  useEffect(() => {
    setData(getPostByNo(no));
    window.scrollTo(0,0);
  }, [ ]);

  return (
    <NoticeBlock>
      <Header onSelect={onSelect} />
        <InfoBlock>
          <div className="post-view-wrapper">
            {
              data ? (
                <>
                    <h2>{data.title}</h2>
                    <h3>{data.createDate}</h3>
                    <div>
                      <img src={data.content} alt=""></img>
                    </div>
                    <p>{data.text} </p>
                </>
              ) : '해당 게시글을 찾을 수 없습니다.'
            }
            <div>
              <button className="post-view-go-list-btn" onClick={() => history.goBack()}><GiHamburgerMenu className="goback" />목록보기</button>
            </div>
          </div>
        </InfoBlock>
      <Footer /> 
    </NoticeBlock>
  )
}

export default PostView;
