import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import Map from './Map';
import '../index.css';

const StoreBlock = styled.div`
    padding: 150px 0; background: #f2f2f2;
    h2 {font-size: 38px; text-align: center; margin-bottom: 50px;}
    Map {position: relative;}
    .search { z-index: 9999999;  box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.19);
            animation: ani 3s ease-in-out 0s infinite alternate;
            position: absolute; top: 400px; right: 50px; padding-top: 30px;
                   background: #fff; width: 300px; height: 380px;
        .searchform {border: 2px solid #ddd; display: imline-block; vertical-align: middle;
                    margin: 0 25px;
            input {padding: 10px; border: none; background: none;
                   font-size: 13px; text-indent: 10px;}
            img {float: right; transform: translateY(60%); padding-right: 10px;}
        }
        @keyframes ani {0% { transform:translateY(-40px) } 100% { transform:translateY(20px) }}
    }



`;

const Storesection = () => {
    return (
        <StoreBlock>
            <h2>매장찾기</h2>
            <Map />
            <div className="search">
                <div className="searchform">
                    <form id="mapFrm" method="POST" name="mapFrm" onsubmit="return shopMap.search()">
                        <input id="keyword" type="text" maxlength="30" placeholder="지역 또는 매장명 입력" />
                        <Link to="#none"><img src="images/icon_search.png" alt="" /></Link>
                    </form>
                </div>
            </div>
        </StoreBlock>
    );
};

export default Storesection;