// import React, {useEffect} from 'react';
// import {Link} from 'react-router-dom';
import styled from 'styled-components';
import '../index.css';
import {ImArrowUp} from "react-icons/im";

const AdvertBlock = styled.div`
    padding: 150px 0; background: #f2f2f2;
    h2 {font-size: 38px; text-align: center; margin-bottom: 50px;}
    .cflist {margin: 0 50px;
        .ad {float: left; width: 32%; margin-right: 2%; text-align: center; 
            &:nth-child(3n+1) {margin-right: 0};
            p {
                text-align: center; font-size: 18px;
                padding: 10px 0 40px; letter-spacing: -0.035em;
                box-sizing: border-box;
            }
        }
    }
    .gotop {
        position: fixed; right: 30px; bottom: 50px; z-index: 99999;
        background: #ffc300; color: #fff; text-align: center; cursor: pointer;
        font-size: 20px; padding: 5px 8px; transition: all 0.5s;
        &:hover {background: #009223}
        }
`;


const Advertsection = () => {

    const onScroll = () => {
        window.scrollTo(0,0)
    }

    return (
        <AdvertBlock>
            <div className="cflist cf">
                <h2>광고영상</h2>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/tqj6XWBeCmQ?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>스파이시 쉬림프 바이럴 쿨제이</p>
                </div>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/fGzhNUaavVY?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>최준의 노부담 로스트 치킨</p>
                </div>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/qZgkqJc4wGs?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>준이 위치 궁금한 사람만 보세요</p>
                </div>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/dMBlQzWPVaA?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>K-바비큐</p>
                </div>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/sik0SYMRjYs?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>로티세리 바비큐 치킨썹</p>
                </div>
                <div className="ad">
                <iframe width="395" height="222" src="https://www.youtube.com/embed/WWKe42xvYa0?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>이 맛, 선 제대로 넘었다!</p>
                </div>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/1ceHGusJiEY?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>돌아온 에그마요</p>
                </div>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/kJ7V1XsJw1U?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>우리 이거.. 같이 먹을랩?</p>
                </div>
                <div className="ad">
                    <iframe width="395" height="222" src="https://www.youtube.com/embed/dzb6oIAuinY?controls=1&amp;mute=1&amp;&amp;rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen />
                    <p>쉬림프 컬렉션</p>
                </div>
            </div>
            <div className="gotop" onClick={onScroll}>
                <ImArrowUp />
            </div>
        </AdvertBlock>
    );
};

export default Advertsection;