import React from 'react';
import styled, {css} from 'styled-components';
import menu from './menu.json';
import MenuItem from './MenuItem';
import {ImArrowUp} from "react-icons/im";

const MenuBlock = styled.div`
    padding: 110px 0; background: #f2f2f2;
    .main {
        background-images: url(images/sandwich/main_sd.jpg) ;
        background-position: 50% 0;
        background-repeat: no-repeat; 
        height: 380px; max-width: 100%;
        color: #fff; text-align: center;
        h2 {
            font-size: 70px;
            font-weight: bold;
            padding: 93px 0 21px 0;
            z-index: 9;}
        span {
            font-size: 16px;
            line-height: 26px; 
            letter-spacing: -0.04em;
            z-index: 9;
        }
    }
    .tabs {display: flex; justify-content: center;}
    .content {overflow: hidden;
              display: flex; justify-content: space-between; flex-wrap: wrap;
              padding: 50px;}
   .gotop {
        position: fixed; right: 30px; bottom: 50px; z-index: 99999;
        background: #ffc300; color: #fff; text-align: center; cursor: pointer;
        font-size: 20px; padding: 5px 8px; transition: all 0.5s;
        &:hover {background: #009223}
        }
`;

const List = styled.div`
    padding: 10px 20px; background: #ddd; border-radius: 10px;
    cursor: pointer; margin: 50px 20px; 
    ${
        props => props.active && css`background: #009223; color: #fff;`
    }
`;

const tabTit = [
    {id:0, title:"샌드위치", part: menu.part1, background:"url(images/main_sd.jpg)", name:"Sandwich", desc1:"전세계 넘버원 브랜드 Subway!", desc2:"50년 전통의 세계 최고의 샌드위치를 맛보세요!"},
    {id:1, title:"랩·기타", part: menu.part2, background:"url(images/main_wp.png)", name:"Wrap", desc1:"Subway를 더 맛있고 간편하게 즐기는 방법" , desc2:"최상의 레시피로 만들어진 써브웨이 랩 시리즈, 이젠 고민하지 마세요!", desc3:"* 그릴드 랩과 파니니는 일부 매장에서만 제공 가능합니다. 매장 리스트는 공지사항을 확인해주세요."},
    {id:2, title:"찹샐러드", part: menu.part3, background:"url(images/main_cs.png)", name:"Chopped Salad", desc1:"양은 더 많이! 칼로리는 더 적게!", desc2:"신선한 야채와 다양한 소스로 가볍게 찹샐러드를 즐겨보세요!"},
    {id:3, title:"아침메뉴", part: menu.part4, background:"url(images/main_mm.png)", name:"Morning Menu", desc1:"아침 메뉴도 Subway와 함께, 취향대로 즐기자!", desc2:"※아침메뉴 제공시간 : 오전 11시까지", desc3:"* 그릴드 랩과 파니니는 일부 매장에서만 제공 가능합니다. 매장 리스트는 공지사항을 확인해주세요."},
    {id:4, title:"스마일썹", part: menu.part5, background:"url(images/main_ss.png)", name:"Smile Sub", desc1:"웃음이 번지는 착한 가격, 착한 맛!", desc2:"스마일 썹과 함께 하루 종일 스마일 하세요~"},
    {id:5, title:"단체메뉴", part: menu.part6, background:"url(images/main_tm.png)", name: "Catering Menu", desc1:"특별한 순간을 더욱 특별하게 해줄 단체 매뉴를 소개합니다.", desc2:"※ 최소 1일전 매장에 주문해주세요."}
]

const Menusection = ({onSelect, mid}) => {

    const onScroll = () => {
        window.scrollTo(0,0)
    }

    return (
        <MenuBlock>
            <div className="main" style={{ backgroundImage:tabTit[mid].background }}>
                <h2>{tabTit[mid].name}</h2>
                <p>{tabTit[mid].desc1}</p>
                <p>{tabTit[mid].desc2}</p>
                <p>{tabTit[mid].desc3}</p>
            </div>
            <div className="tabs">
                {
                    tabTit.map( (data) => <List key={data.id} active={mid===data.id} onClick={ () => onSelect(data.id)}>{data.title}</List> )
                }
            </div>
            <div className="content">
                {
                    tabTit[mid].part.map( (data, index) => <MenuItem key={index} name={data.name} nameen={data.nameen} cal={data.cal} photo={data.photo} summary={data.summary} /> )
                }
            </div>
            <div className="gotop" onClick={onScroll}>
                <ImArrowUp />
            </div>
        </MenuBlock>
    );
};

export default Menusection;