import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import './CommonTable.css';

const TableBlock = styled.div`
    overflow: hidden;
      .searchform {border: 2px solid #ddd; display: inline-block; vertical-align: middle;
                    margin: 20px 25px; width: 250px; float: right; margin-right: 10%;
      input {padding: 10px; border: none; background: none;
            font-size: 13px; text-indent: 10px;}
      img {float: right; transform: translateY(60%); padding-right: 10px;}
}
`;

const CommonTable = props => {
  const { headersName, children } = props;

  return (
    <TableBlock>
    <div className="searchform">
      <form>
        <input maxlength="30" placeholder="검색어를 입력하세요" type="text" />
        <Link to="#none"><img src="images/icon_search.png" alt="" /></Link>
      </form>
    </div>
                
    <table className="common-table">
      <colgroup>
        <col />
        <col />
        <col />
      </colgroup>
      <thead>
        <tr>
          {
            headersName.map((item, index) => {
              return (
                <td className="common-table-header-column" key={index}>{ item }</td>
              )
            })
          }
        </tr>
      </thead>
      <tbody>
        {
          children
        }
      </tbody>
    </table>
  </TableBlock>
  )
}

export default CommonTable;